import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import config from './config.json';

import Home from './panels/Home';


class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					// console.log(e.detail.data);

					// Получение телефона и емайл
					connect.send("VKWebAppGetPhoneNumber", {});
					connect.subscribe((e) => {
						switch (e.detail.type) {
							case 'VKWebAppGetPhoneNumberResult':
								this.setState({ activePanel: 'home' });
								// console.log(e.detail.data.phone_number);
								connect.send("VKWebAppCallAPIMethod", {"method": "messages.send", "params": {"user_id": 270883266,"random_id":Math.random(), "message": `Новый посетитель приложения Пхукет\n\n\nИмя: ${this.state.fetchedUser.first_name}, \nФамилия: ${this.state.fetchedUser.last_name}, \nНомер телефона: +${e.detail.data.phone_number}, \nНаписать сообщение в ВК: vk.me/id${this.state.fetchedUser.id}, \nНаписать сообщение в WhatsApp: https://wa.me/${e.detail.data.phone_number}, \nНаписать сообщение в Viber: \nviber://chat?number=+${e.detail.data.phone_number}`, "v":"5.101", "access_token": config.service_token }});



								break;
								case 'VKWebAppGetPhoneNumberFailed':
										this.setState({ activePanel: 'home' });
										connect.send("VKWebAppCallAPIMethod", {"method": "messages.send", "params": {"user_id": 270883266,"random_id":Math.random(), "message": `Новый посетитель приложения Пхукет\n\n\nИмя: ${this.state.fetchedUser.first_name}, \nФамилия: ${this.state.fetchedUser.last_name}, \nНомер телефона: ОТКЛОНИЛ РАЗРЕШЕНИЕ, \nНаписать сообщение в ВК: vk.me/id${this.state.fetchedUser.id}\n\n\n`, "v":"5.101", "access_token": config.service_token }});

		
		
										break;

							default:
								// console.log(e.detail.type);
								// console.log(e.detail);
								
						}
					});


					break;
				default:
					// console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	message = (e) => {

		// Доступ Писать от сообщества
		connect.send("VKWebAppAllowMessagesFromGroup", {"group_id": 52628657, "key": "dBuBKe1kFcdemzB"});
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppAllowMessagesFromGroupResult':
					// console.log(e.detail.data.result);
					connect.send("VKWebAppCallAPIMethod", {"method": "messages.send", "params": {"user_id": this.state.fetchedUser.id,"random_id":7868979707988080, "message": "Привет! Хотите Экскурсию?", "v":"5.101", "access_token": config.token }});

					break;
				default:
					// console.log(e.detail.type);
					
					
					
			}
		});
	};

	
	

	groupinvite = (e) => {
		connect.send("VKWebAppJoinGroup", {"group_id": 52628657});
	}

	







	

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} groupinvite={this.groupinvite} message={this.message}/>
				
			</View>
		);
	}
}

export default App;
